@charset "utf-8";
/* CSS Document */

@import "mdc.fab.css";
body,
td,
th,
p,
input,
textarea {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10pt;
  color: #333;
  line-height: 1.5;
}

ul,
li {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10pt;
  color: #333;
}

a {
  color: orange;
  text-decoration: none;
}

#skip-link a {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

#skip-link a:focus {
  position: static;
  width: auto;
  height: auto;
}

a:hover,
a:focus {
  color: #3397db;
  text-decoration: none;
}

a img {
  border: none 0px #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0 0 20px;
}

article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
input,
menu,
section,
textarea {
  display: block;
}

header {
  position: relative;
  flex: 0 1 auto;
}

body {
  display: flex;
  height: 100vh;
  flex-direction: column;
  background-color: #fdfdfd;
  margin: 0;
  position: relative;
}

#maincontent {
  background-color: #f3f3f3;
  display: flex;
  flex-flow: column nowrap;
  flex: 1 1;
  position: relative;
  overflow-y: scroll;
}

#footer {
  position: relative;
  background-color: #444;
  color: #b8b5b5;
  font-size: 8pt;
  letter-spacing: 1px;
  padding: 15px;
  text-align: center;
  text-transform: uppercase;
}

/* ====================== Navigation ====================== */

nav {
  width: 100%;
  height: auto;
  /* min-height: 80px; */
  /* max-height: 80px; */
  background-color: #252831;
  text-align: left;
}

nav h1 {
  margin: auto;
  padding: 10px 0;
  text-align: center;
}

nav h1 a {
  color: #fff;
  font-size: 1em;
  font-weight: 200;
  text-transform: uppercase;
}

#breadcrumb {
  padding: 5px 0px 5px 10px;
  list-style: none;
  background-color: #eee;
  font-size: 17px;
  margin: 0px;
}

nav #breadcrumb [aria-current="page"] {
  color: #000;
  font-weight: 700;
  text-decoration: none;
}

/* Display list items side by side */

#breadcrumb li {
  display: inline;
}

/* Add a slash symbol (/) before/behind each list item */

#breadcrumb li+li:before {
  padding: 8px;
  color: black;
  content: "/\00a0";
}

/* Add a color to all links inside the list */

#breadcrumb li a {
  color: #0404ff;
  text-decoration: none;
}

/* Add a color on mouse-over */

#breadcrumb li a:hover {
  color: #01447e;
  text-decoration: underline;
}

/* ====================== Map ====================== */

#map-container {
  position: relative;
  height: 40vh;
  min-height: 40vh;
}

#map {
  height: 100%;
  width: 100%;
  background-color: #ccc;
}

.inactive {
  display: none;
}

.blur {
  background: url('data:image/jpeg;base64,') 100%;
  background-size: cover;
  height: 100%;
  overflow: hidden;
  filter: blur(2px);
}

#image-blurred {
  position: relative;
  /* don't use height: 100% because cause problem with resolution > 850 (flex) */
}

.center {
  position: absolute;
  top: 50%;
  /* don't use height: 100% because cause problem with resolution > 850 (flex) */
  width: 100%;
  padding-bottom: 50%;
  text-align: center;
  color: gray;
}

/* ====================== Restaurant Details ====================== */

.inside #maincontent {
  flex: 1 1 auto;
}

.inside #map {
  background-color: #ccc;
  height: 100%;
  width: 100%;
}

#restaurant-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-flow: column nowrap;
  flex: 1 0;
  align-content: flex-start;
}

#restaurant-name {
  color: #9c2900;
  font-family: Arial, sans-serif;
  font-size: 5vw;
  font-weight: 200;
  letter-spacing: 0;
  margin: 20px 0 15px;
  padding: 0 15px;
  text-transform: uppercase;
  line-height: 1.1;
  text-align: left;
  width: calc(100% - 30px);
}

#restaurant-img {
  border-radius: 5px;
  width: calc(100% - 20px);
  height: auto;
  display: block;
  margin: 10px auto;
}

#restaurant-info {
  flex: 1;
  display: flex;
  flex-flow: column wrap;
}

#restaurant-details {
  flex: 2;
}

#restaurant-address {
  font-size: 4vw;
  margin: 10px 15px;
}

#restaurant-cuisine {
  background-color: #333;
  color: #ddd;
  font-size: 12pt;
  font-weight: 300;
  letter-spacing: 3px;
  padding: 2px 0;
  text-align: center;
  text-transform: uppercase;
}

.restaurant-day {
  font-weight: bold;
  padding: 0 15px 0 0;
}

#restaurant-hours {
  margin: 0 15px;
}

#restaurant-hours td {
  color: #666;
  font-size: 3.5vw;
}

#restaurant-container,
#reviews-container {
  border-bottom: 1px solid #d9d9d9;
  border-top: 1px solid #fff;
  border-left: 1px solid #d9d9d9;
  width: 100%;
}

#reviews-container h2 {
  color: #9c2900;
  font-size: 5vw;
  font-weight: 300;
  letter-spacing: -1px;
  margin: 10px 15px;
  text-align: left;
}

#reviews-list {
  margin: 0;
  padding: 0;
}

#no-reviews {
  margin: 15px;
  color: black;
  font-size: 2vw;
}

#reviews-list li {
  border-top-left-radius: 25px;
  background-color: #fff;
  border: 2px solid #f3f3f3;
  display: block;
  list-style-type: none;
  margin: 15px;
  overflow-y: hidden;
  position: relative;
}

#reviews-list .reviews-header {
  display: block;
  background: black;
  height: auto;
  margin-bottom: 15px;
  padding-bottom: 10px;
}

#reviews-list .reviews-name {
  display: block;
  font-size: 3.5vw;
  float: left;
  color: whitesmoke;
  width: auto;
}

#reviews-list .reviews-date {
  display: block;
  font-size: 3vw;
  text-align: right;
  color: whitesmoke;
}

#reviews-list li p {
  margin: 0;
  padding: 10px 20px 5px;
  font-size: 3.5vw;
}

#reviews-list li.addReview {
  margin: 15px;
  width: auto;
}

#reviews-list li.addReview .reviews-header h3 {
  color: white;
  padding: 10px 0 0 20px;
  display: block;
  margin: auto;
}

#reviews-list li.addReview textarea {
  margin: 20px 20px 55px;
  padding: 10px 20px 5px;
  font-size: 3.5vw;
  width: calc(100% - 82px);
  resize: vertical;
  min-height: 30px;
}

#reviews-list li.addReview input.reviews-name {
  color: black;
  margin: 5px 0px 5px 20px;
  padding: 10px 20px 5px;
  font-size: 3.5vw;
  width: 31%;
  height: calc(90% - 23px);
}

#reviews-list .reviews-rating-score {
  background: #f0ab07;
  color: #040404;
}

#reviews-list .reviews-rating {
  display: inline-block;
  margin: 3px 15px 0px 20px;
  padding: 5px 10px;
  border-radius: 5%;
  background: #f0ab07;
  color: #040404;
  border: 3px groove #eee;
  text-align: center;
  text-transform: uppercase;
}

.app-fab--favorite {
  background-color: whitesmoke;
  color: grey;
  position: fixed;
  bottom: 8rem;
  right: 1.5rem;
}

.app-fab--isfavorite {
  color: red;
}

.app-fab--hide {
  display: none;
}

.app-fab--add {
  background-color: red;
  position: fixed;
  bottom: 5rem;
  right: 1.5rem;
}

/* The toast */

#toast {
  visibility: hidden;
  width: calc(100% - 32px);
  background-color: #333;
  color: whitesmoke;
  text-align: left;
  font-weight: 400;
  font-size: 4vw;
  border-radius: 2px;
  padding: 16px;
  position: fixed;
  z-index: 1;
  bottom: 0px;
}

#toast.show {
  visibility: visible;
}

/* offline icon */

#offline {
  color: white;
  padding: 0 0 0 15px;
  display: none;
}

#offline.show {
  display: initial;
}

.rew-fab--save {
  background-color: blue;
  position: absolute;
  bottom: 0.5rem;
  right: 4.5rem;
}

.rew-fab--delete {
  background-color: darkgray;
  position: absolute;
  bottom: 0.5rem;
  right: 1.5rem;
}

#display-none {
  display: none;
}

#reviews-list li i.review-offline {
  display: block;
  font-size: 7vw;
  text-align: right;
  color: white;
  padding: 6px 13px 0px;
  margin: 0;
}